import React, { useEffect, useRef } from 'react';
import './InvitationText.css';  // CSS 파일 임포트

const InvitationText = () => {
  const textRef = useRef(null);

  // AOS 초기화
  useEffect(() => {
    import('aos').then((AOS) => {
      AOS.init();
    });
  }, []);

  return (
    <div
      ref={textRef} // ref 추가
      data-aos="fade-up" 
      data-aos-duration="1500"
    >
      <h2>INVITATION</h2>
      <p>서로 다른 길을 걸어온 저희가 함께하는</p>
      <p>다섯 번째 겨울을 끝으로</p>
      <p>부부의 연을 맺어 항상 함께하기로 했습니다</p>
      <p className="extra-space">인생의 또 다른 시작을 하기로 한 날</p>
      <p>귀한 걸음 하시어 축복해 주시면 감사하겠습니다</p>
      <div className="names">
        <p>성천모 . 이미자의 아들 성기덕</p>
        <p>노장열 . 주정하의 딸 노승혜</p>
      </div>
    </div>
  );
};

export default InvitationText;
